.board {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.row {
  display: flex;
}

.cell {
  width: 30px;
  height: 30px;
  border: 1px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.cell:hover {
  background-color: #ddd;
}
.history {
  margin-top: 20px;
}

.history h3 {
  margin: 0;
  padding: 0;
}

.history ul {
  list-style: none;
  padding: 0;
}

.history li {
  margin: 5px 0;
}